import React, { useState, useEffect } from 'react';
import { Link, useParams, useHistory } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import {
  getMember,
  selectMember,
  updateMemberAuth,
  selectIsGettingMember,
} from '../../redux/reducers/memberReducer';
import styled from 'styled-components';
import {
  Layout,
  Breadcrumb,
  Button,
  Typography,
  Divider,
  Select,
  Space,
  Table,
  Tag,
  Descriptions,
} from 'antd';
import Loading from '../../components/Loading';
import { translateAuth } from '../../utils';

const { Content } = Layout;
const { Option } = Select;
const { Title, Text } = Typography;

const InfoHeader = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
`;

const MemberContent = styled(Content)`
  padding: 24px;
  background-color: ${(props) => props.theme.colors.white};
`;

const columns = [
  {
    title: ' ID',
    dataIndex: 'CourseId',
    key: 'CourseId',
    render: (text) => text,
  },
  {
    title: '',
    key: 'Course',
    dataIndex: 'Course',
    render: (Course) => Course.CourseTitle,
  },
];

const AuthSelection = ({ selectedUserId, handleChange }) => {
  const authTypeIdList = [1, 2, 3];
  return (
    <>
      <Select
        style={{ width: 120 }}
        onChange={handleChange}
        defaultValue={translateAuth(selectedUserId)}
      >
        {authTypeIdList.map((authTypeId) => (
          <Option key={authTypeId} value={authTypeId}>
            {translateAuth(authTypeId)}
          </Option>
        ))}
      </Select>
    </>
  );
};

export default function MemberDetailPage() {
  const { id } = useParams();
  const history = useHistory();
  const dispatch = useDispatch();
  const [localAuthTypeId, setLocalAuthTypeId] = useState(0);
  const member = useSelector(selectMember);
  const isGettingMember = useSelector(selectIsGettingMember);

  useEffect(() => {
    dispatch(getMember(id));
  }, [dispatch, id]);

  function handleChange(value) {
    setLocalAuthTypeId(value);
  }

  const handleSave = () => {
    if (localAuthTypeId === 0 || localAuthTypeId === member.authTypeId) return;
    dispatch(updateMemberAuth(id, localAuthTypeId));
    history.push(`/console/members`); // 
  };

  return (
    <>
      {isGettingMember && <Loading />}
      {!isGettingMember && member && (
        <>
          <InfoHeader>
            <Breadcrumb style={{ margin: '16px 0' }}>
              <Breadcrumb.Item>
                <Link to="/console/members"></Link>
              </Breadcrumb.Item>
              <Breadcrumb.Item> (ID: {member.id})</Breadcrumb.Item>
            </Breadcrumb>
            <Space size="large">
              <AuthSelection
                handleChange={handleChange}
                selectedUserId={member.authTypeId}
              />
              <Button type="primary" onClick={handleSave}>
                
              </Button>
            </Space>
          </InfoHeader>
          <MemberContent>
            <Descriptions title="" bordered>
              <Descriptions.Item label=" ID">{member.id}</Descriptions.Item>
              <Descriptions.Item label="" span={2}>
                {translateAuth(member.authTypeId)}
              </Descriptions.Item>
              <Descriptions.Item label="">
                {member.email}
              </Descriptions.Item>
              <Descriptions.Item label="" span={2}>
                {member.nickname}
              </Descriptions.Item>
              <Descriptions.Item label="">
                {new Date(member.createdAt).toLocaleString()}
              </Descriptions.Item>
              <Descriptions.Item label="" span={2}>
                {new Date(member.updatedAt).toLocaleString()}
              </Descriptions.Item>
              <Descriptions.Item label="" span={3}>
                {member.courseList.length > 0 ? (
                  <Table
                    columns={columns}
                    dataSource={member.courseList.map((item) => ({
                      ...item,
                      key: item.CourseId,
                    }))}
                  />
                ): (
                  <div></div>
                )}
              </Descriptions.Item>
            </Descriptions>
          </MemberContent>
        </>
      )}
    </>
  );
}
