import React, {
  useEffect,
  useRef,
  useImperativeHandle,
  forwardRef,
} from 'react';
import { Form, Input, Button, Modal, message } from 'antd';
import styled from 'styled-components';
import { useDispatch } from 'react-redux';
import {
  setUserErrorMessage,
  updateMyPassword,
  logout,
} from '../../../redux/reducers/userReducer';
import { useHistory } from 'react-router';
import { isStrongPassword } from '../../../utils';

const BoxWrapper = styled.div`
  text-align: center;
`;
const layout = {
  labelCol: {
    span: 8,
  },
  wrapperCol: {
    span: 12,
  },
};
const tailLayout = {
  wrapperCol: {
    span: 24,
  },
};
const validateMessages = {
  required: '',
};

const UpdateForm = forwardRef(({ setModalText }, ref) => {
  const [form] = Form.useForm();
  const dispatch = useDispatch();
  const history = useHistory();

  useImperativeHandle(ref, () => ({
    resetForm: () => {
      form.resetFields();
      dispatch(setUserErrorMessage(null));
    },
  }));

  const handleFinish = (value) => {
    const { oldPassword, newPassword } = value;
    message.loading({
      content: '',
      key: 'isUpdating',
      duration: 0,
    });

    dispatch(updateMyPassword(oldPassword, newPassword)).then((json) => {
      //  
      if (json.ok === 1) {
        message.success({
          content: '',
          key: 'isUpdating',
          duration: 5,
        });
        setModalText('');
        return setTimeout(() => {
          dispatch(logout());
          history.push('/login');
        }, 3000);
      }
      // ,
      message.error({
        content: json.errorMessage,
        key: 'isUpdating',
        duration: 5,
      });
    });
  };

  return (
    <BoxWrapper>
      <Form
        form={form}
        {...layout}
        name="updatePassword"
        onFinish={handleFinish}
        validateMessages={validateMessages}
      >
        <Form.Item
          label=""
          name="oldPassword"
          rules={[{ required: true }]}
        >
          <Input.Password />
        </Form.Item>
        <Form.Item
          label=""
          name="newPassword"
          rules={[
            { required: true },
            ({ getFieldValue }) => ({
              validator(_, inputValue) {
                if (!inputValue || isStrongPassword(inputValue)) {
                  return Promise.resolve();
                }
                return Promise.reject('!');
              },
            }),
          ]}
          extra="820"
        >
          <Input.Password />
        </Form.Item>
        <Form.Item
          label=""
          name="confirm"
          dependencies={['newPassword']}
          rules={[
            { required: true },
            ({ getFieldValue }) => ({
              validator(_, inputValue) {
                if (
                  !inputValue ||
                  getFieldValue('newPassword') === inputValue
                ) {
                  return Promise.resolve();
                }
                return Promise.reject('!');
              },
            }),
          ]}
          extra=""
        >
          <Input.Password />
        </Form.Item>
        <Form.Item {...tailLayout}>
          <Button htmlType="submit" type="primary">
            
          </Button>
        </Form.Item>
      </Form>
    </BoxWrapper>
  );
});

export default function ModalUpdatePassword({ visible, setVisible }) {
  const ChildRef = useRef();
  const closeModal = () => {
    setVisible(false);
    ChildRef.current.resetForm();
  };
  const [modalText, setModalText] = React.useState();

  useEffect(() => {
    setModalText(<UpdateForm setModalText={setModalText} ref={ChildRef} />);
  }, []);

  return (
    <Modal
      title=""
      onCancel={closeModal}
      afterClose={() => setUserErrorMessage(null)}
      visible={visible}
      footer={null}
      closable
    >
      {modalText}
    </Modal>
  );
}
